import React from "react";
import { HStack, VStack, Text } from "native-base";
import { vh, vw } from "../../../util";

const PageHeader = ({ title, subtitle, rightElement, noborder = false }) => {
  let bottomBorder = {
    borderBottomColor: "rgba(0,0,0, 0.15)",
    borderBottomWidth: vw(1.5),
    marginBottom: vh(20),
  };
  if (noborder) {
    bottomBorder = {};
  }
  return (
    <HStack
      style={{ paddingBottom: vh(16), ...bottomBorder }}
      justifyContent={"space-between"}
    >
      <VStack>
        <Text
          style={{
            fontSize: vw(24),
            color: "#303030",
            fontFamily: "Poppins_medium",
          }}
        >
          {title}
        </Text>
        <Text
          style={{
            fontSize: vw(13),
            color: "#666666",
            fontFamily: "Poppins",
          }}
        >
          {subtitle}
        </Text>
      </VStack>
      {rightElement}
    </HStack>
  );
};

export default PageHeader;
