import React, { useState, useRef, useEffect, useCallback } from "react";
import {
  Center,
  VStack,
  HStack,
  Text,
  Pressable,
  Spinner,
  FlatList,
  ChevronUpIcon,
  // useMediaQuery,
  Hidden,
} from "native-base";
import { useForm } from "react-hook-form";
import moment from "moment-timezone";

import PageHeader from "./PageHeader";
import { vh, vw } from "../../../util";
import Colors from "../../../constants/Colors";
import { getUser, API_URL } from "../../../constants/Config";
import ShadowButton from "../../../components/ShadowButton";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import ShadowPagination from "../../../components/ShadowPagination";

const useMountedState = () => {
  const mountedRef = useRef(false);
  const isMounted = useCallback(() => mountedRef.current, []);

  useEffect(() => {
    mountedRef.current = true;

    return () => {
      mountedRef.current = false;
    };
  }, []);

  return isMounted;
};

const FeatureItem = ({ item, _showAlert, setRequests, setPage }) => {
  const [isVoting, setIsVoting] = useState(false);
  // let fc = "blue.500";
  // if (item.status === "completed") {
  //   fc = "green.700";
  // } else if (item.status === "planned") {
  //   fc = "yellow.500";
  // } else if (item.status === "later") {
  //   fc = "gray.400";
  // } else if (item.status === "started") {
  //   fc = "violet.700";
  // }

  const saveVoting = async () => {
    try {
      const { token } = getUser();
      setIsVoting(true);
      const result = await fetch(`${API_URL}/features/vote/${item._id}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
      });
      const response = await result.json();
      if (response.success) {
        if (response.data) {
          const rd = response.data;
          if (item.votes > rd.votes) {
            _showAlert("success", "You unvoted successfully.");
          } else {
            _showAlert("success", "You voted successfully.");
          }
          setRequests((pr) => {
            if (pr.length === 1) {
              if (item.votes > rd.votes) {
                setPage(1);
              }
            }
            return pr.map((r) => {
              if (r._id === item._id) {
                return rd;
              } else {
                return r;
              }
            });
          });
        }
      } else {
        console.log(response.error);
        _showAlert(
          "error",
          "An error occured, cannot vote for the feature request."
        );
      }
      setIsVoting(false);
    } catch (err) {
      setIsVoting(false);
      _showAlert(
        "error",
        "An error occured, cannot vote for the feature request."
      );
      console.log(err.message);
    }
  };

  return (
    <HStack
      style={{
        boxShadow: "0px 3px 10px #0000001A",
        paddingHorizontal: vw(10),
        paddingVertical: vh(10),
        width: vw(584),
        marginHorizontal: vw(10),
        marginVertical: vh(10),
        borderRadius: 10,
        // flex: 1,
        backgroundColor: "white",
      }}
    >
      <VStack
        style={{
          height: vh(90),
          width: vw(80),
          borderRadius: 10,
          backgroundColor: "rgba(53, 23, 93, 0.5)",
          justifyContent: "center",
          alignSelf: "center",
        }}
      >
        <Pressable
          onPress={() => {
            saveVoting();
          }}
        >
          {isVoting ? (
            <Center>
              <Spinner size={"sm"} color={Colors.primary} />
            </Center>
          ) : (
            <>
              <Center style={{ marginTop: vh(10) }}>
                <ChevronUpIcon color='white' />
              </Center>
              <Text
                style={{
                  fontSize: vw(24),
                  fontFamily: "Poppins",
                  color: "white",
                  marginBottom: vh(4),
                  marginTop: vh(5.5),
                  textAlign: "center",
                }}
              >
                {item.votes}
              </Text>
              <Text
                style={{
                  fontSize: vw(13),
                  fontFamily: "Poppins_medium",
                  color: "white",
                  marginBottom: vh(4),
                  textAlign: "center",
                }}
              >
                VOTES
              </Text>
            </>
          )}
        </Pressable>
      </VStack>
      <VStack style={{ flex: 1 }}>
        <HStack
          style={{
            margin: vh(5),
            justifyContent: "space-between",
            borderBottomColor: "rgba(210, 210, 210, 1)",
            borderBottomWidth: vh(2),
            paddingBottom: vh(12),
            height: vh(60),
          }}
        >
          <Text
            style={{
              color: "rgba(48, 48, 48, 1)",
              fontSize: vw(15),
              fontFamily: "Poppins_medium",
            }}
          >
            {item.title}
          </Text>
          {/* <Center style={{ width: vw(100) }}> */}
          <Text
            rounded={"full"}
            style={{
              height: vh(24),
              backgroundColor: "rgba(181, 152, 219, 1)",
              fontFamily: "Poppins_medium",
              fontSize: vw(12),
              paddingHorizontal: vw(10),
              lineHeight: vh(24),
              color: "white",
            }}
          >
            {item.status}
          </Text>
          {/* </Center> */}
        </HStack>
        <HStack style={{ justifyContent: "space-between" }}>
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(12),
              paddingHorizontal: vw(10),
              color: "rgba(167, 167, 167, 1)",
            }}
          >
            {item.createdby.firstname + " " + item.createdby.lastname}
          </Text>
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(12),
              paddingHorizontal: vw(10),
              color: "rgba(167, 167, 167, 1)",
            }}
          >
            {moment(item.created).fromNow()}
          </Text>
        </HStack>
      </VStack>
    </HStack>
  );
};

const Features = ({ _showAlert }) => {
  const fRef = useRef();
  // const [landscape, portrait] = useMediaQuery([
  //   {
  //     orientation: "landscape",
  //   },
  //   {
  //     orientation: "portrait",
  //   },
  // ]);
  const [tab, setTab] = useState(0);
  const [total, setTotal] = useState(0);
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [requests, setRequests] = useState([]);
  const [page1, setPage1] = useState(1);
  const [page2, setPage2] = useState(1);
  const [page3, setPage3] = useState(1);
  const [limit, setLimit] = useState(10);
  const isMounted = useMountedState();

  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "all",
    defaultValues: {},
  });

  const onSubmit = async (data) => {
    try {
      setIsSaving(true);
      const { token } = getUser();
      const body = {
        title: data.title,
      };
      const result = await fetch(API_URL + "/features/add", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(body),
      });
      const response = await result.json();
      if (response.success) {
        _showAlert("success", "New feature request saved successfully.");
      } else {
        console.log("failed", response.error);
        _showAlert("error", response.error);
        setIsSaving(false);
        return;
      }
      reset();
      setIsSaving(false);
      setIsOpen(false);
    } catch (err) {
      setIsSaving(false);
      _showAlert("error", "An error occured, cannot save the feature request.");
      console.log(err.message);
    }
  };

  useEffect(() => {
    const getFeatures = async () => {
      try {
        setIsLoading(true);
        let tabPage = page1;
        if (tab === 0) {
          tabPage = page1;
        } else if (tab === 1) {
          tabPage = page2;
        } else if (tab === 2) {
          tabPage = page3;
        }

        console.log("getFeatures", "Tab", tab, "Page", tabPage);
        const { token, _id } = getUser();
        const filter = {};
        const body = {
          skip:
            parseInt(tabPage) === 1
              ? 0
              : parseInt(tabPage - 1) * parseInt(limit),
          limit: parseInt(limit),
        };

        body["sort"] = { created: 1 };

        if (tab === 1) {
          filter.createdby = _id;
          body["sort"] = { created: 1, votes: -1 };
        }

        if (tab === 2) {
          body["sort"] = { votes: -1, created: 1 };
          filter.topvoted = true;
        }

        body["filter"] = filter;

        const result = await fetch(API_URL + "/features/get", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + token,
          },
          body: JSON.stringify(body),
        });
        const response = await result.json();

        setRequests(response.data);
        setTotal(response.count);
        setIsLoading(false);
        return response;
      } catch (err) {
        setIsLoading(false);
        console.log(err.message);
      }
    };

    if (isMounted && !isSaving) {
      getFeatures();
    }
  }, [page1, page2, page3, limit, tab, isSaving, isMounted]);

  const NoItemsFound = () => {
    return (
      <Center height={"50.7vh"} style={{ backgroundColor: "#F7F8FA" }}>
        <Text>No items found.</Text>
      </Center>
    );
  };

  const cardwidth = 500;
  let cols = Math.floor((window.screen.width - 295) / cardwidth);
  if (cols === 0) {
    cols = 1;
  }

  const ph = 100 - parseFloat(vh(70)) + "vh";

  return (
    <VStack style={{ minHeight: ph }}>
      <PageHeader
        // noborder
        title={"Feature Requests"}
        // subtitle='Hay muchas variaciones de los pasajes de Lorem Ipsum disponibles'
        rightElement={
          <VStack>
            <Hidden only={["sm", "base"]}>
              <ShadowButton
                space={2}
                variant='outline'
                label='New Request'
                fontSize={vw(13)}
                style={{ height: vh(32), marginTop: vh(5) }}
                color='rgba(102, 102, 102, 1)'
                onPress={() => {
                  reset();
                  setIsOpen(true);
                }}
              />
            </Hidden>
            <Hidden from={"md"}>
              <ShadowButton
                variant='outline'
                label='+'
                fontSize={vw(18)}
                style={{ height: vh(32), marginTop: vh(5), minWidth: vw(20) }}
                color={Colors.primary}
                onPress={() => {
                  setIsOpen(true);
                }}
              />
            </Hidden>
          </VStack>
        }
      />
      {/* Feature Filter Tabs */}
      <HStack
        style={{
          borderBottomColor: "rgba(0,0,0, 0.15)",
          borderBottomWidth: vw(1.5),
          justifyContent: "center",
        }}
      >
        <HStack justifyContent={"space-around"} space={8}>
          <Pressable
            onPress={() => {
              setTab(0);
            }}
          >
            <Text
              style={{
                color: tab === 0 ? Colors.primary : "#666666",
                fontSize: vw(15),
                fontFamily: "Poppins_medium",
                paddingBottom: vh(15),
                borderBottomColor: Colors.primary,
                borderBottomWidth: tab === 0 ? vw(3) : 0,
                paddingHorizontal: vw(10),
                // minWidth: vw(50),
                textAlign: "center",
              }}
            >
              All
            </Text>
          </Pressable>
          <Pressable
            onPress={() => {
              setTab(1);
            }}
          >
            <Text
              style={{
                color: tab === 1 ? Colors.primary : "#666666",
                fontSize: vw(15),
                fontFamily: "Poppins_medium",
                paddingBottom: vh(15),
                borderBottomColor: Colors.primary,
                borderBottomWidth: tab === 1 ? vw(3) : 0,
                paddingHorizontal: vw(10),
                textAlign: "center",
              }}
            >
              My Features
            </Text>
          </Pressable>
          <Pressable
            onPress={() => {
              setTab(2);
            }}
          >
            <Text
              style={{
                color: tab === 2 ? Colors.primary : "#666666",
                fontSize: vw(15),
                fontFamily: "Poppins_medium",
                paddingBottom: vh(15),
                borderBottomColor: Colors.primary,
                borderBottomWidth: tab === 2 ? vw(3) : 0,
                paddingHorizontal: vw(10),
                textAlign: "center",
              }}
            >
              Top Voted
            </Text>
          </Pressable>
        </HStack>
      </HStack>

      <VStack flex={1}>
        {isLoading ? (
          <Center height={"full"} justifyContent='center' alignItems='center'>
            <Spinner size='small' color={"#7D2BE9"} />
          </Center>
        ) : cols === 1 ? (
          <FlatList
            key={"list-" + cols}
            style={{ marginLeft: vw(-10) }}
            contentContainerStyle={{
              paddingBottom: vh(20),
              // minHeight: "59.2vh",
            }}
            ListEmptyComponent={<NoItemsFound />}
            data={requests}
            keyExtractor={(item) => item._id}
            renderItem={({ item }) => (
              <FeatureItem
                setPage={setPage3}
                item={item}
                _showAlert={_showAlert}
                setRequests={setRequests}
                cols={cols}
              />
            )}
            maxToRenderPerBatch={limit}
            initialNumToRender={limit}
            // columnWrapperStyle={{ justifyContent: "flex-start" }}
            // numColumns={cols}
          />
        ) : (
          <FlatList
            key={"list-" + cols}
            style={{ marginLeft: vw(-5) }}
            contentContainerStyle={{
              paddingBottom: vh(20),
              // minHeight: "59.2vh",
            }}
            ListEmptyComponent={<NoItemsFound />}
            data={requests}
            keyExtractor={(item) => item._id}
            renderItem={({ item }) => (
              <FeatureItem
                setPage={setPage3}
                item={item}
                _showAlert={_showAlert}
                setRequests={setRequests}
                cols={cols}
              />
            )}
            maxToRenderPerBatch={limit}
            initialNumToRender={limit}
            columnWrapperStyle={{ justifyContent: "flex-start" }}
            numColumns={cols}
          />
        )}
        {total > 0 && tab === 0 && (
          <ShadowPagination
            total={total}
            limit={limit}
            page={page1}
            setLimit={setLimit}
            setPage={setPage1}
            view={false}
          />
        )}
        {total > 0 && tab === 1 && (
          <ShadowPagination
            total={total}
            limit={limit}
            page={page2}
            setLimit={setLimit}
            setPage={setPage2}
            view={false}
          />
        )}
        {total > 0 && tab === 2 && (
          <ShadowPagination
            total={total}
            limit={limit}
            page={page3}
            setLimit={setLimit}
            setPage={setPage3}
            view={false}
          />
        )}
      </VStack>

      <ShadowModal
        placement='top'
        size='lg'
        header='New Feature'
        // desc='Edit this to change the appearance of the owner name on your
        //     postcard.'
        isOpen={isOpen}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={2}
              label='Cancel'
              variant='outline'
              onPress={() => {
                reset();
                setIsOpen(false);
              }}
              style={{
                height: vh(36),
              }}
            />
            <ShadowButton
              space={3}
              isLoading={isSaving}
              label='Save'
              onPress={handleSubmit(onSubmit)}
              style={{
                height: vh(36),
              }}
            />
          </HStack>
        }
      >
        <VStack style={{ height: 100, marginTop: vh(-20) }}>
          <ShadowInput
            ref={fRef}
            multiline
            numberOfLines={3}
            height={vh(80)}
            isRequired
            name='title'
            label={"Enter your feature request "}
            placeholder='Enter your feature request'
            control={control}
            rules={{
              required: true,
              message: "Please enter your feature request",
            }}
            maxLength={500}
            invalid={errors.title ? true : false}
            error={errors.title ? errors.title.message : ""}
            inputStyle={{}}
          />
        </VStack>
      </ShadowModal>
    </VStack>
  );
};

export default Features;
