import React, { useState } from "react";
import { Input, FormControl } from "native-base";
import { Controller } from "react-hook-form";
import Util from "../util";
const { vw, vh } = Util;

const ShadowInput = React.forwardRef((props, ref) => {
  return (
    <FormControl
      isRequired={props.isRequired}
      isInvalid={props.invalid}
      isReadOnly={props.isReadOnly || false}
      style={{
        height: vh(48),
        ...props.style,
      }}
    >
      {props.label && (
        <FormControl.Label
          style={{
            fontSize: vw(13),
            fontFamily: "Poppins_medium",
            color: props.invalid ? "red.500" : "rgba(102, 102, 102, 0.7)",
            height: vh(19),
            ...props.labelstyle,
          }}
        >
          {props.label}
        </FormControl.Label>
      )}

      <Controller
        control={props.control}
        rules={props.rules}
        name={props.name}
        style={{
          zIndex: 1,
        }}
        render={({ field: { onChange, onBlur, value } }) => (
          <Input
            isReadOnly={props.isReadOnly || false}
            ref={ref}
            size={props.size || "lg"}
            multiline={props.multiline || false}
            numberOfLines={props.numberOfLines || 1}
            minLength={props.minLength || 1}
            maxLength={props.maxLength || 255}
            isInvalid={props.invalid}
            autoComplete='off'
            autoFocus={props.autoFocus || false}
            type={props.type || "text"}
            onSubmitEditing={props.onSubmitEditing}
            onKeyPress={props.onKeyPress}
            onChangeText={(value) => {
              onChange(value);
              if (props.onChangeText) {
                props.onChangeText();
              }
            }}
            value={value || ""}
            variant={"outline"}
            invalidOutlineColor={"red.300"}
            focusOutlineColor={"rgba(0, 0, 0, 0.15)"}
            _invalid={{
              borderColor: "red.300",
              borderWidth: "1",
            }}
            _light={{
              borderWidth: "1",
            }}
            _dark={{
              borderWidth: "1",
            }}
            _web={{
              borderWidth: "1",
            }}
            style={{
              fontSize: vw(15),
              fontFamily: "Poppins_medium",
              backgroundColor: "white",
              zIndex: 1,
              height: props.height || vh(36),
            }}
            InputLeftElement={props.leftIcon || null}
            InputRightElement={props.rightIcon || null}
            placeholder={props.placeholder}
          />
        )}
      />
      <FormControl.ErrorMessage style={{ marginTop: vh(-3) }}>
        {props.error}
      </FormControl.ErrorMessage>
      <FormControl.HelperText style={{ marginTop: vh(-3) }}>
        {props.help}
      </FormControl.HelperText>
    </FormControl>
  );
});

export default ShadowInput;
