import React from "react";
import {
  HStack,
  Text,
  Modal,
  Pressable,
  useBreakpointValue,
  VStack,
} from "native-base";

import ShadowButton from "./ShadowButton";
import CloseIcon from "../components/svg/CloseIcon";

import Util from "../util";
const { vw, vh } = Util;

const ShadowModal = React.forwardRef((props, ref) => {
  const mw = useBreakpointValue({
    base: "90%",
    lg: 500,
    xl: 500,
    md: 500,
    sm: "90%",
  });

  const styles = {
    top: {
      marginBottom: "auto",
      marginTop: 100,
    },
    bottom: {
      marginBottom: 5,
      marginTop: "auto",
    },
    left: {
      marginLeft: 0,
      marginRight: "auto",
    },
    right: {
      marginLeft: "auto",
      marginRight: 0,
    },
    center: {},
  };

  return (
    <Modal
      size={props.size || "full"}
      isOpen={props.isOpen}
      onClose={props.onClose}
      initialFocusRef={props.initialFocusRef}
    >
      <Modal.Content
        style={{
          ...props.style,
          ...styles[props.placement || "center"],
          borderRadius: vw(20),
        }}
      >
        <Modal.Header
          style={{
            backgroundColor: "white",
            borderBottomWidth: 0,
          }}
        >
          <HStack flex={1} justifyContent={"space-between"}>
            <VStack style={{ width: "80%" }}>
              <Text
                style={{
                  fontSize: vw(20),
                  color: "#303030",
                  fontFamily: "Poppins_medium",
                }}
              >
                {props.header}
              </Text>
              {props.desc && (
                <Text
                  width={["60%", "100%"]}
                  style={{
                    fontSize: vw(13),
                    color: "rgba(0,0,0, 0.5)",
                    fontFamily: "Poppins",
                    flexWrap: "wrap",
                    wordWrap: "wrap",
                  }}
                >
                  {props.desc}
                </Text>
              )}
            </VStack>

            {props.close && (
              <Pressable
                zIndex={0}
                onPress={props.close}
                _focus={{
                  width: vw(36),
                  height: vh(36),
                  marginTop: vh(-5),
                  boxShadow: "0px 2px 8px rgba(0, 0, 0, 0.1) !important",
                }}
                style={{
                  width: vw(36),
                  height: vh(36),
                  marginTop: vh(-5),
                  boxShadow: "0px 2px 8px rgba(0, 0, 0, 0.1)",
                }}
                // shadow={2}
                rounded={"full"}
                justifyContent='center'
                alignItems={"center"}
              >
                <CloseIcon />
              </Pressable>
            )}
          </HStack>
        </Modal.Header>
        <Modal.Body
          style={{
            backgroundColor: "white",
            // width: props.width || mw,
            // paddingHorizontal: vw(20),
            minHeight: props.bodyHeight || vh(50),
            // flex: 1,
          }}
        >
          {props.children}
        </Modal.Body>
        {props.buttons && (
          <Modal.Footer
            paddingX={vw(40)}
            borderTopWidth={0}
            backgroundColor={"white"}
            style={props.footerStyle}
          >
            {props.buttons ? (
              props.buttons
            ) : (
              <>
                <ShadowButton
                  color='#7D2BE9'
                  variant='outline'
                  label={"Cancel"}
                  onPress={props.cancel}
                />
                <ShadowButton
                  label={"Save"}
                  isLoading={props.isLoading}
                  onPress={props.save}
                />
              </>
            )}
          </Modal.Footer>
        )}
      </Modal.Content>
    </Modal>
  );
});

export default ShadowModal;
