import React, { useRef } from "react";
import { Button } from "native-base";

import { vw, vh } from "../util";

const ShadowButton = React.forwardRef((props, ref) => {
  let dw = props.label.length * 10;
  const space = props.space ? props.space * 10 : 10;
  dw = dw + space;
  return (
    <Button
      width={vw(dw || 100)}
      rounded={"full"}
      _text={{
        color: props.variant ? "rgba(0,0,0,0.5)" : "white",
        fontSize: vw(15),
        fontFamily: "Poppins",
      }}
      _focus={{
        height: vh(36),
        width: vw(dw || 100),
        _text: {
          color: props.variant ? "rgba(0,0,0,0.3)" : "white",
          fontFamily: "Poppins",
          fontSize: vw(15),
        },
        _important: {
          background:
            "transparent linear-gradient(124deg, rgba(186, 71, 243, 1) 0%, rgba(125, 43, 233, 1) 100%) 0% 0% no-repeat padding-box",
        },
        background:
          "transparent linear-gradient(124deg, rgba(186, 71, 243, 1) 0%, rgba(125, 43, 233, 1) 100%) 0% 0% no-repeat padding-box",
        ...props._focus,
      }}
      _pressed={{
        height: vh(36),
        width: vw(dw || 100),
        _text: {
          color: props.variant ? "rgba(0,0,0,0.3)" : "white",
          fontFamily: "Poppins",
          fontSize: vw(15),
        },
        background: props.variant
          ? "white"
          : "transparent linear-gradient(124deg, rgba(186, 71, 243, 1) 0%, rgba(125, 43, 233, 1) 100%) 0% 0% no-repeat padding-box",
        ...props._pressed,
        // backgroundColor:
        //   "transparent linear-gradient(124deg, rgba(186, 71, 243, 1) 0%, rgba(125, 43, 233, 1) 100%) 0% 0% no-repeat padding-box",
      }}
      size={"md"}
      variant={"unstyled"}
      {...props}
      style={{
        width: vw(dw || 100),
        height: vh(36),
        background: props.variant
          ? "white"
          : "transparent linear-gradient(124deg, rgba(186, 71, 243, 1) 0%, rgba(125, 43, 233, 1) 100%) 0% 0% no-repeat padding-box",
        ...props.style,
      }}
    >
      {props.label}
    </Button>
  );
});

export default ShadowButton;
