import React, { useState, useEffect } from "react";
import { HStack, Select, Center, Pressable, Text } from "native-base";
import ReactPaginate from "react-paginate";
import styled from "styled-components";

import { vh, vw } from "../util";
import PrevIcon from "./svg/PrevIcon";
import NextIcon from "./svg/NextIcon";
import Colors from "../constants/Colors";
import DropDownIcon from "./svg/DropDownIcon";

const ShadowPagination = ({
  total = 1,
  limit = 10,
  setLimit,
  setPage,
  page,
  setIsLoading,
  style,
  view = true,
  
}) => {
  const pageCount = Math.ceil(total / limit);

  const onPageChange = (p) => {
    setPage(p.selected + 1);
  };

  const MyPaginate = styled(ReactPaginate).attrs({
    activeClassName: "active", // default to "disabled"
  })`
    margin-bottom: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    list-style-type: none;
    padding: 0 1rem;
    li a {
      border-radius: 4px;
      padding: 0.1rem 1rem;
      cursor: pointer;
      color: rgba(102, 102, 102, 1);
      font-family: "Poppins_medium";
      font-size: ${vw(15)};
    }
    li.previous a,
    li.next a,
    li.break a {
      border-color: transparent;
      color: rgba(102, 102, 102, 1);
      font-family: "Poppins_medium";
      font-size: ${vw(15)};
    }
    li.active a {
      background-color: #7d2be9;
      border-color: transparent;
      color: white;
      min-width: 24px;
      font-family: "Poppins_medium";
      font-size: ${vw(15)};
    }
    li.disabled a {
      color: rgba(102, 102, 102, 0.5);
    }
    li.disable,
    li.disabled a {
      cursor: default;
    }
  `;

  return (
    <HStack
      style={{
        boxShadow: "0px 0px 15px rgba(0, 0, 0, 0.1)",
        justifyContent: "space-between",
        alignItems: "center",
        height: vh(60),
        backgroundColor: "white", //"rgba(247, 248, 250, 1)",
        ...style,
      }}
    >
      {view ? (
        <HStack style={{ alignItems: "center", paddingHorizontal: vh(20) }}>
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "rgba(102, 102, 102, 1)",
              marginRight: vw(10),
            }}
          >
            View
          </Text>
          <Select
            outlineColor={"rgba(0,0,0.0.15)"}
            outlineStyle={{
              borderWidth: vh(2),
            }}
            variant='outline'
            selectedValue={parseInt(limit)}
            style={{
              width: vw(50),
            }}
            onValueChange={(v) => {
              setPage(1);
              setLimit(v);
            }}
            dropdownIcon={<DropDownIcon />}
          >
            <Select.Item label={"1"} value={1} />
            <Select.Item label={"5"} value={5} />
            <Select.Item label={"10"} value={10} />
            <Select.Item label={"20"} value={20} />
            <Select.Item label={"30"} value={30} />
            <Select.Item label={"40"} value={40} />
            <Select.Item label={"50"} value={50} />
            <Select.Item label={"100"} value={100} />
          </Select>
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "rgba(102, 102, 102, 1)",
              marginLeft: vw(10),
            }}
          >
            per page
          </Text>
        </HStack>
      ) : (
        <HStack></HStack>
      )}
      <MyPaginate
        breakLabel='...'
        nextLabel='Next >'
        onPageChange={onPageChange}
        pageRangeDisplayed={3}
        marginPagesDisplayed={2}
        pageCount={pageCount}
        previousLabel='< Previous'
        renderOnZeroPageCount={null}
        forcePage={page - 1}
      />
    </HStack>
  );
};

export default React.memo(ShadowPagination);
